.p-category:before { content: "#" }

#trash {
  border-top: 1px solid #909090;
  margin-top: 100px;
  position: relative;
  .icon--trash {
    left: 50%;
    margin-left: -27px;
    position: relative;
    top: -31px;
  }
}

.redirects .save-redirects {
  margin-right: 25px;
}

#deleted-pages {
  @include clearfix;
  position: relative;
}

.ui-sortable-connected {
  @include clearfix;
  position: relative;
  min-height: 320px;
}

.table th {
  font-weight: normal;
}

.table--small {
  border: 1px solid #aaa;
  width: auto;
  thead {
    border-bottom: 2px solid #aaa;
  }
  tbody tr {
    border-bottom: 1px solid #aaa;
  }
  th {
    text-align: left;
  }
  th,
  td {
    padding: .5em 1em;
    &:first-child {
    }
  }
}

// -------------------------------------
//   flash styles
// -------------------------------------

.alert{
  width: 100%;
  color: #ffffff;
  padding: 10px 0;
  &.alert-success{
    background: #5cb85c;
  }
  &.alert-danger{
    background: #d9534f;
  }
}


